<template>
  <v-container>
    <h4 class="my-4">
      <span class="cblu bbred">All Videos</span>
    </h4>
    <v-row>
      <v-col cols="12" md="3">
        <!-- <CatSidebar/> -->
        <!-- left sidebar -->
          <div class="mobile">
            <div class="d-flex justify-content-between">
              <!-- <v-btn small @click="price">Price</v-btn> -->
              <v-btn small @click="deals">Near by Videos</v-btn>
              <!-- <v-btn small @click="category">Change Category</v-btn> -->
            </div>
          </div>
          <div class="catside shadow desktop">
            <!-- <v-autocomplete solo dense label="Category" class="mt-3" placeholder="Category"
                v-model="selectSubCategoty"
                @change="selectCategory()"
                :items="categories"
                item-value="sub_cat_id"
                item-text="subcat_name"
                id="sel1" ></v-autocomplete>
            <v-autocomplete solo dense label="Location" class="mt-3" placeholder="Location"
                v-model="selectCity"
                :items="locations" 
                item-value="city_id"
                item-text="city"
                id="sel1" ></v-autocomplete> -->
            <div class="h5 sec1 mt-3">Near by Videos</div>
            <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
              <div class="text-center small">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
            <v-btn color="success" class="my-2">Go</v-btn>
          </div>
          <v-dialog v-model="dialog1" persistent>
            <v-card>
              <span class="d-flex justify-content-between">
                  <v-card-title>Price</v-card-title>
                  <v-icon @click="dialog1 = false" class="me-3">mdi-close</v-icon>
              </span>
              <hr class="m-0">
              <v-card-text>
                <v-range-slider
                    v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
                <span class="d-flex">
                      <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                      <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
                    </span>
              </v-card-text>
              <div class="text-center">
                <b-button class="mb-3" variant="success" type="submit">Go</b-button>
              </div>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialog2" persistent>
            <v-card>
              <span class="d-flex justify-content-between">
                  <v-card-title>Deals Radar</v-card-title>
                  <v-icon @click="dialog2 = false" class="me-3">mdi-close</v-icon>
              </span>
              <hr class="m-0">
              <v-card-text>
                <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
                <div class="text-center h6 link-dark">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
              </v-card-text>
              <div class="text-center">
                <b-button class="mb-3" variant="success" type="submit">Go</b-button>
              </div>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialog3" persistent class="p-2">
            <v-card>
              <span class="d-flex justify-content-between">
                  <v-card-title>Change Category</v-card-title>
                  <v-icon @click="dialog3 = false" class="me-3">mdi-close</v-icon>
              </span>
              <hr class="m-0">
              <v-card-text class="pb-0">
                <v-autocomplete solo dense :items="cats" label="Category" class="mt-3" placeholder="Category"></v-autocomplete>
              </v-card-text>
              <div class="text-center">
                <b-button class="mb-3" variant="success" type="submit">Go</b-button>
              </div>
            </v-card>
          </v-dialog>
      </v-col>
      <v-col cols="12" md="9">
        <div class="mobile">
          <v-card v-for="(video, i) in allvideos" :key="i" class="item mitem my-2 mb-5">
            <div class="small d-flex">
              <div class="mimg">
                <iframe v-if="video.video_url!=''" :src=video.video_url cover class="rounded-3" width="100" height="100" allowfullscreen></iframe>
                <v-img v-if="video.video_url==''" :src="('https://placehold.co/600x400?text='+video.video_title)" cover class="rounded-3" height="100"></v-img>
              </div>
              <div class="mdetails">
                    <router-link :to="{name:'videodetail',params:{video_id:video.video_id}}" class="mtitle link-dark">
                        {{ video.video_title }}
                    </router-link>
                    <div>Add to Wishlist<v-icon size="20">mdi-heart-outline</v-icon></div>
                    <div class="ratings align-center d-flex">
                      <v-rating :value=video.rating_avrage color="amber" dense half-increments size="20"></v-rating>
                      <div class="white--text badge bg-dark ms-2">{{ video.total_reviews }}</div>
                    </div>
                    <div class="address">
                      <v-icon small>mdi-map-marker</v-icon>
                        <div v-if="video.address!=null">
                            {{ video.address }}, 
                        </div>
                          {{ video.city }}, {{ video.state }}
                    </div>
                    <router-link :to="{name:'videodetail',params:{video_id:video.video_id}}" class="cred">Read More</router-link>
              </div>
                <div class="mcontact">
                    <a :href="('https://wa.me/'+video.mobile_phone+'?text=rf')" target="_blank">
                      <v-btn fab small color="success" class="mb-5">
                      <v-icon small>mdi-whatsapp</v-icon></v-btn>
                    </a>
                    <a :href="('tel:'+ video.mobile_phone)">
                      <v-btn fab small color="primary">
                        <v-icon small>mdi-phone</v-icon></v-btn>
                    </a>
                    
                    <!-- <v-btn fab small color="success" class="mb-5"><v-icon>mdi-whatsapp</v-icon></v-btn>
                    <v-btn fab small color="primary"><v-icon>mdi-phone</v-icon></v-btn> -->
                </div>
            </div>
        </v-card>
      </div>
      <div class="desktop" v-if="rlists.length > 1">
        <v-card v-for="(video, i) in rlists" :key="i" :perPage="perPage">
          <v-row class="mb-3">
            <v-col cols="12" md="3" class="py-0">
              <router-link :to="{name:'videodetail',params:{video_id:video.video_id}}" class="link-dark">
                  <iframe v-if="video.video_url!=''" :src=video.video_url cover width="225" allowfullscreen></iframe>
                  <v-img v-if="video.video_url==''" :src="('https://placehold.co/600x400?text='+video.video_title)" cover height="100%"></v-img>
              </router-link>
            </v-col>
            <v-col cols="12" md="9" class="py-0">
              <v-row>
                <v-col cols="12" md="7" class="py-0">
                  <v-card-text class="">
                    <h6 class="word">
                        <router-link :to="{name:'videodetail',params:{video_id:video.video_id}}" class="link-dark">
                            {{ video.video_title }}
                        </router-link>
                    </h6>
                    <div>Add to Wishlist<v-icon>mdi-heart-outline</v-icon></div>
                    <div class="ratings align-center d-flex">
                      <v-rating :value=video.rating_avrage color="amber" dense half-increments size="20" readonly></v-rating>
                      <div class="white--text badge bg-dark ms-2">{{ video.total_reviews }}</div>
                    </div>
                    <div class="address">
                      <v-icon small>mdi-map-marker</v-icon>
                        <div v-if="video.address!=null">
                            {{ video.address }}, 
                        </div>
                          {{ video.city }}, {{ video.state }}
                    </div>
                    <router-link :to="{name:'videodetail',params:{video_id:video.video_id}}" class="cred">Read More</router-link>
                  </v-card-text>
                </v-col>
                <v-col cols="12" md="5">
                  <v-card-text>
                    <a :href="('tel:'+ video.mobile_phone)">
                      <v-btn left small color="primary" class="me-1">
                        <v-icon small>mdi-phone</v-icon>Call Now</v-btn>
                    </a>
                    <a :href="('https://wa.me/'+video.mobile_phone+'?text=rf')" target="_blank">
                      <v-btn small color="success">
                      <v-icon small>mdi-whatsapp</v-icon>WhatsApp</v-btn>
                    </a>
                  </v-card-text>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
        <v-pagination v-if="rlists.length > 1" class="pagination" v-model="page" :length="Math.ceil(allvideos.length/perPage)" circle></v-pagination>
      </div>
    </v-col>
    </v-row>
  </v-container>
</template>

<script>
// import CatSidebar from "@/views/CatSidebar";
import axios from 'axios';
import { EventBus } from "@/event-bus";
export default {
  name: "AllVideo",
  data() {
    return {
      url: 'https://bizlx.itechvision.in',
      page:1,
      perPage:10,
      allvideos: [],
      dialog1: false,
      dialog2: false,
      dialog3: false,
      catvalue:null,
      selected: null,
      min: 0,
      max: 120000,
      range: [500, 10000],
      range1: [0, 100],
      min1: 0,
      max1: 100,
      searchedCatData: []
    };
  },
  mounted(){
      EventBus.$on('event_bus_emitted', (data) =>{
          this.allvideos = data.allVideos;
      });
      const config = {
          headers: { 'content-type': 'multipart/form-data' }
      }
      this.searchedCatData = JSON.parse(localStorage.getItem("catSearchedValues"));
      axios.post("search/category", this.searchedCatData, config)
          .then((resp) => {
            if(resp.data.dealsType=="videos"){ // deals page
                this.allvideos = resp.data.allVideos;
            }else{
              this.$router.push({name: 'home'});
            }
          },
      );
  },
  methods: {
    price() {
      this.dialog1 = true;
    },
    deals() {
      this.dialog2 = true;
    },
    category() {
      this.dialog3 = true;
    }
  },
  computed:{
    rlists(){
      return this.allvideos.slice((this.page - 1)*this.perPage, this.page*this.perPage)
    }
  },


};
</script>
<style scoped>
.word{ word-break: normal; }
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 110px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 0px 0 0 7px;
  line-height: 1.1;
}
.mcontact {
  width: 42px;
}
.fs11{font-size: 10.4px;}

.catside{
  background: #e5e5e5;
  padding: 10px;
}
.sec1{
  background: #f2f2f2;
  padding: 5px 15px;
  margin-bottom: 20px;
}
</style>
